import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/inventory/itemRequest/actions";

import Popconfirm from "../../../../components/feedback/popconfirm";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  DatePicker,
  Empty,
  Select,
  Divider,
  Spin,
  Row,
  Col,
  // Button as ButtonAntd,
  Steps,
  Tooltip,
  Icon
} from "antd";

import EditableTable from "./editableTable";
import ModalTransaction from "./modal";
import ModalClose from "./modalClose";
// import { Object } from "core-js";
import { customDot } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const Step = Steps.Step;

const { getItems, setPrintData, confirmItemRequest, closeItemRequest } = actions;

class ItemRequestUpdateForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id_location: null,
      item: null,
      items: [],
      itemDetails: {
        item_request_details: []
      },
      total_qty: 0,
      search: null,
      visible: false,
      visibleModalClose: false,
      clear: false,
      clearedItems: false,
      err: false,
      err_item: false
    };
  }

  componentDidUpdate(prevProps, prevState) {
    const { getItems } = this.props;
    const { itemRequest } = this.props.itemRequest;
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingForeign
    } = this.props.itemRequest;
    const { itemDetails, total_qty, clearedItems } = this.state;

    if (
      !clearedItems &&
      !loading &&
      !loadingSubmit &&
      !loadingItems &&
      !loadingTransactions &&
      !loadingForeign
    ) {
      getItems(itemRequest.id_location);
      this.setState({
        id_location: itemRequest.id_location,
        items: itemRequest.item_request_details
      });
    }

    if (prevState.itemDetails.item_request_details !== itemDetails.item_request_details) {
      const { setPrintData } = this.props;
      setPrintData({ ...itemDetails, total_qty: total_qty});
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { itemDetails } = this.state;

      // validasi item
      if (itemDetails.item_request_details.length <= 0) {
        return this.setState({ err_item: true });
      } else {
        this.setState({ err_item: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      const data = Object.assign({}, values, itemDetails);
      // console.log(data);
      if (!err) {
        this.setState({ err: false });
        this.props.submit(data);
      }
    });
  };

  handleSelectLocation = id_location => {
    const { getItems } = this.props;
    this.setState({
      id_location: id_location,
      clear: this.state.id_location !== id_location ? true : false
    });

    getItems(id_location);
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleModalClose = () => {
    this.setState({
      visibleModalClose: !this.state.visibleModalClose
    });
  };

  handleSelectItem = value => {
    const { items } = this.props.itemRequest;
    const { search } = this.state;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    const { getItems } = this.props;
    // console.log(items.filter(item => item.id_item === value));
    this.setState({
      item: items.find(item => item.id_item === value)
    });

    if (search) {
      getItems(id_location);
      this.setState({ search: null });
    }
  };

  handleSelectModal = value => {
    const { transactions } = this.props.itemRequest;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_item_request === value
    );

    transaction.item_request_details.map(data => {
      data.id_item_request = null;
      data.id_item_request_detail = null;
      data.ordered_qty = 0;
    });

    this.setState({
      items: transaction.item_request_details,
      itemDetails: {
        item_request_details: transaction.item_request_details
      },
      clear: false
    });
    setFieldsValue({ transaction_no: transaction.transaction_no });
    this.handleModal();
  };

  handleSearchItem = value => {
    const { getItems } = this.props;
    const { getFieldValue } = this.props.form;
    const id_location = getFieldValue("id_location");
    // console.log(this.props.purchaseInvoice);
    this.setState({ search: value });
    if (id_location) {
      getItems(id_location, value);
    }
  };

  handleChangeItem = value => {
    this.setState({ search: value });
  };

  handleSaveItemList = (value, total_qty) => {
    this.setState({
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false
    });
  };

  clearItem = (value, total_qty) => {
    this.setState({
      item: null,
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false
    });
  };

  clearItems = (value, total_qty) => {
    this.setState({
      items: [],
      itemDetails: {
        item_request_details: value
      },
      total_qty: total_qty,
      clear: false,
      clearedItems: true
    });
  };

  render() {
    // console.log(this.props.itemRequest.transactions);
    const { search, visible } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { itemRequest } = this.props;
    const { items, locations, departments, projects } = itemRequest
      ? itemRequest
      : [];
    const { user } = this.props;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const {
      loading,
      loadingSubmit,
      loadingItems,
      loadingTransactions,
      loadingForeign
    } = itemRequest;
    const data = itemRequest.itemRequest ? itemRequest.itemRequest : [];

    // const used = data.is_used;
    const error = itemRequest.errorField ? itemRequest.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const transactionStatus = () => {
      let curr = 0;
      switch (data.transaction_status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 3:
          curr = 2;
          break;
        case 4:
          curr = 3;
          break;
        case 5:
          curr = 4;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <Row style={{ margin: "12px 0px" }}>
          <Col span={20}>
            <Steps
              size="small"
              progressDot={customDot}
              current={transactionStatus()}
            >
              <Step title={getLanguage({ id: "new" })} />
              <Step title={getLanguage({ id: "requested" })} />
              <Step title={getLanguage({ id: "progress" })} />
              <Step title={getLanguage({ id: "fulfilled" })} />
              <Step title={getLanguage({ id: "closed" })} />
            </Steps>
          </Col>
        </Row>

        <ModalTransaction
          visible={visible}
          itemRequest={itemRequest}
          id_location={getFieldValue("id_location")}
          handleModal={this.handleModal}
          handleSelectModal={this.handleSelectModal}
        />

        <ModalClose
          visible={this.state.visibleModalClose}
          handleModal={this.handleModalClose}
          submit={this.props.handleSubmitClose}
          loading={loadingSubmit}
        />

        <Row>
          <Col span={12} style={{ paddingRight: "15px" }}>
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <Tooltip
                      placement="top"
                      title={
                        <IntlMessages id="itemRequests.transactionNo.help" />
                      }
                    >
                      <IntlMessages id="itemRequests.transactionNo" />
                      <Icon
                        style={{ marginLeft: "5px" }}
                        type="question-circle"
                        theme="filled"
                      />
                    </Tooltip>
                  }
                >
                  {getFieldDecorator("transaction_no", {
                    initialValue: data.transaction_no
                  })(<Input disabled={true} />)}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="itemRequests.transactionDate" />}
                  style={{ marginBottom: 0, paddingBottom: 0 }}
                >
                  {getFieldDecorator("transaction_date", {
                    initialValue: moment(data.transaction_date)
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime={{ format: "HH:mm:ss" }}
                      format="DD MMMM YYYY HH:mm:ss"
                      disabled={
                        !permissions.includes("Can Back Date") ||
                        data.transaction_status !== 1
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemRequests.location" />}
              >
                {getFieldDecorator("id_location", {
                  initialValue: data.id_location,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    showSearch
                    style={{ width: "100%" }}
                    placeholder={<IntlMessages id="itemRequests.location" />}
                    loading={loadingForeign}
                    onSelect={this.handleSelectLocation}
                    disabled={data.transaction_status !== 1}
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {`${data.location_code} (${data.location_name})`}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Col>
          <Col span={12} style={{ paddingLeft: "15px" }}>
            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="itemRequests.note" />}
            >
              {getFieldDecorator("note", {
                initialValue: data.note
              })(
                <TextArea
                  disabled={data.transaction_status !== 1}
                  autosize={{ minRows: 4 }}
                />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="itemRequests.subtitle.itemList" />
        </Divider>

        <Row>
          <Col span={24}>
            <Form.Item
              {...formItemLayout}
              validateStatus={
                error.item_request_details || this.state.err_item 
                  ? "error" 
                  : null
              }
              help={
                error.item_request_details 
                  ? error.item_request_details 
                  : this.state.err_item
                    ? <IntlMessages id="itemDataEmpty" />
                    : null
              }
              extra={<IntlMessages id="itemRequests.item.help" />}
            >
              <Select
                value={search ? search : []}
                showSearch
                allowClear
                // optionFilterProp="children"
                filterOption={false}
                placeholder={
                  <IntlMessages id="itemRequests.item.placeholder" />
                }
                loading={loadingItems}
                notFoundContent={
                  loadingItems ? (
                    <Spin size="small" />
                  ) : getFieldValue("id_location") ? (
                    <Empty description={<IntlMessages id="noData" />} />
                  ) : (
                    <Empty
                      description={
                        <IntlMessages id="itemRequests.item.noLocation" />
                      }
                    />
                  )
                }
                onSelect={this.handleSelectItem}
                onSearch={this.handleSearchItem}
                disabled={data.transaction_status !== 1}
                value={undefined}
              >
                {items.map(data => (
                  <Option key={data.id_item} value={data.id_item}>
                    {data.item_code + " - " + data.item_name}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={24}>
            <EditableTable
              item={this.state.item}
              items={this.state.items}
              departments={departments}
              projects={projects}
              transaction_status={data.transaction_status}
              handleSaveItemList={this.handleSaveItemList}
              clearItem={this.clearItem}
              clearItems={this.clearItems}
              loadingTransactions={loadingTransactions}
              loadingItems={loadingItems}
              clear={this.state.clear}
              status={data.transaction_status}
            />
          </Col>
        </Row>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "50px", marginRight: "5px" }}
            loading={loading || loadingSubmit}
            disabled={data.transaction_status <= 1 ? false : true}
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* Confirm */}
          <Popconfirm
            title={<IntlMessages id="confirmTransaction" />}
            onConfirm={() =>
              this.props.confirmItemRequest(data.id_item_request)
            }
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.transaction_status !== 1 ||
                !permissions.includes("Confirm Item Request")
                  ? true
                  : false
              }
              loading={loading || loadingSubmit}
            >
              <IntlMessages id="button.confirmTransaction" />
            </Button>
          </Popconfirm>

          {/* Transfer */}
          {/* <Popconfirm
            title={<IntlMessages id="confirmItemTransfer" />}
            onConfirm={e => e.preventDefault()}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          >
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={data.transaction_status !== 2 ? true : false}
              loading={loading || loadingSubmit}
            >
              <IntlMessages id="button.itemTransfer" />
            </Button>
          </Popconfirm> */}

          {/* Close */}
          {/* <Popconfirm
            title={<IntlMessages id="confirmCloseTransaction" />}
            onConfirm={() => this.props.closeItemRequest(data.id_item_request)}
            okText={<IntlMessages id="yes" />}
            cancelText={<IntlMessages id="no" />}
          > */}
            <Button
              style={{ marginRight: "5px", padding: "0px 15px" }}
              disabled={
                data.transaction_status === 5
                  ? true
                  : false
              }
              loading={loading || loadingSubmit}
              onClick={this.handleModalClose}
            >
              <IntlMessages id="button.closeTransaction" />
            </Button>
          {/* </Popconfirm> */}

          <br />

          {this.state.err ? (
            <span style={{ color: "#f5222d" }}>
              <IntlMessages id="form.error" />
            </span>
          ) : null}

          {this.state.err_item ? (
            <IntlMessages id="itemDataEmpty">
              {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
            </IntlMessages>
          ) : null}
        </Form.Item>
      </Form>
    );
  }
}

const WrappedItemRequestUpdateForm = Form.create({
  name: "update_item_request"
})(ItemRequestUpdateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    { getItems, setPrintData, confirmItemRequest, closeItemRequest }
  )
)(WrappedItemRequestUpdateForm);
