import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField
  // FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    const column = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "index_no",
        align: "center"
      },
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "item_code"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        render: text => {
          if (text && text.length > 25) {
            return text.substring(0, 25) + "...";
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "qoh" }),
        dataIndex: "current_qty",
        align: "right",
        // render: text => <NumberFormat value={text} />
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "min" }),
        dataIndex: "preference_item.min_stock",
        align: "right",
        // render: text => <NumberFormat value={text} />
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "max" }),
        dataIndex: "preference_item.max_stock",
        align: "right",
        render: (text, row) =>
          text && row.item_type === 1 ? (
            <>
              <NumberFormat value={text} /> {row.base_unit_code}
            </>
          ) : (
            "-"
          )
      },
      {
        title: getLanguage({ id: "rop" }),
        dataIndex: "preference_item.reorder_point",
        align: "right",
        render: (text, row) =>
          text && row.item_type === 1 ? (
            <>
              <NumberFormat value={text} /> {row.base_unit_code}
            </>
          ) : (
            "-"
          )
      },
      {
        title: getLanguage({ id: "sent" }),
        dataIndex: "sent_qty",
        align: "right",
        // render: text => <NumberFormat value={text} />
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "ordered" }),
        dataIndex: "ordered_qty",
        align: "right",
        // render: text => <NumberFormat value={text} />
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      },
      // {
      //   title: getLanguage({ id: "bsUnt" }),
      //   dataIndex: "base_unit_code"
      // },
      {
        title: getLanguage({ id: "request" }),
        dataIndex: "request_qty",
        align: "right",
        // render: text => <NumberFormat value={text} />
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.unit_code}
          </>
        )
      },
      {
        title: getLanguage({ id: "baseQty" }),
        dataIndex: "base_qty",
        align: "right",
        render: (text, row) => (
          <>
            <NumberFormat value={text} /> {row.base_unit_code}
          </>
        )
      }
    ];

    const total = [
      {
        label: (
          <>
            <IntlMessages id="totalQuantity" />
            <IntlMessages id="bsUnt">{txt => ` (${txt})`}</IntlMessages>
          </>
        ),
        amount: <NumberFormat value={data.total_qty} />
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.itemRequest" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.transaction_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                            data.transaction_date,
                            "YYYY-MM-DD HH:mm:ss"
                          ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="itemRequests.location" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.location ? data.location.location_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.item_request_details}
                rowKey={row => row.id_item_request_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() =>
            (document.title = getFileName(
              "item_request",
              data.transaction_no,
              true
            ))
          }
          onAfterPrint={() => (document.title = siteConfig.documentTitle)}
        />
      </>
    );
  }
}

export default Print;
