import React, { Component } from "react";
import { Timeline, Modal } from "antd";
import { Link } from "react-router-dom";

import theme from "../../../../settings/themes/themedefault";
import IntlMessages from "../../../../components/utility/intlMessages";

const redirectPO = "/dashboard/purchases/purchase-orders";
// const redirectPR = "/dashboard/purchases/purchase-receipts";
const redirectIT = "/dashboard/inventories/item-transfers";

class HistoryItemRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [],
      desc: null
    };
  }

  componentDidUpdate(prevProps) {
    const { description } = this.props;
    const { desc } = this.state;
    if (prevProps.description !== desc && description) {
      const spliteds = description.split("|").reverse();

      this.setState({
        history: [...spliteds],
        desc: description
      });
    }
  }

  render() {
    const { history } = this.state;
    // console.log(history);
    return (
      <Modal
        title={<IntlMessages id="itemRequests.history" />}
        centered
        visible={this.props.visible}
        footer={false}
        width="50%"
        onCancel={this.props.handleModalHistory}
      >
        <Timeline mode="alternate">
          {history.map((data, key) => {
            if (data.indexOf("{") >= 0 && data.indexOf("PO") >= 0) {
              const str = data.substr(0, data.indexOf("{"));
              const obj = data.substr(data.indexOf("{"), data.indexOf("}"));
              const po = JSON.parse(obj);
              return (
                <Timeline.Item key={key} color={theme.palette.status[1]}>
                  <span>{str}</span>
                  <br />
                  <span>PO: </span>
                  <Link to={`${redirectPO}/update/${po.id}`}>
                    {po.transaction_no}
                  </Link>
                </Timeline.Item>
              );
            } else if (
              data.indexOf("Updated") >= 0 ||
              data.indexOf("Created") >= 0
            ) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[0]}>
                  {data}
                </Timeline.Item>
              );
            } else if (
              data.indexOf("Requested") >= 0 ||
              data.indexOf("Confirmed") >= 0
            ) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[8]}>
                  {data}
                </Timeline.Item>
              );
            } else if (data.indexOf("Transferred") >= 0) {
              const info = data.slice(0, data.indexOf("{"));
              const it = JSON.parse(data.slice(data.indexOf("{")));

              return (
                <Timeline.Item key={key} color={theme.palette.status[4]}>
                  {info}
                  <br />
                  <span>IT: </span>
                  <Link to={`${redirectIT}/update/${it.id}`}>
                    {it.item_transfer_no}
                  </Link>
                </Timeline.Item>
              );
            } else if (data.indexOf("Closed") >= 0) {
              const info = data.slice(0, data.indexOf("{"));
              const reason = JSON.parse(data.slice(data.indexOf("{")));
              return (
                <Timeline.Item key={key} color={theme.palette.status[3]}>
                  {info}
                  <br />
                  <span>Reason: {reason.closed_reason}</span>
                </Timeline.Item>
              );
            } else if (data.indexOf("Uploaded") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[6]}>
                  {data}
                </Timeline.Item>
              );
            } else if (data.indexOf("Deleted") >= 0) {
              return (
                <Timeline.Item key={key} color={theme.palette.status[7]}>
                  {data}
                </Timeline.Item>
              );
            }
          })}
        </Timeline>
      </Modal>
    );
  }
}

export default HistoryItemRequest;
