import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import ItemRequestUpdateForm from "./updateForm";
import ItemRequestDocUpdateForm from "./updateDocForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

// import theme from "../../../../settings/themes/themedefault";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/itemRequest/actions";
import { Box } from "../../../App/app.style";

import HistoryItemRequest from "./history";
import Print from "./print";
const TabPane = Tabs.TabPane;

const redirecTo = "/dashboard/inventories/item-requests";

class UpdateItemRequest extends Component {
  state = {
    visible: false
  };
  componentDidMount() {
    const { editItemRequest } = this.props;
    const { id } = this.props.match.params;
    editItemRequest(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemRequest.error) {
      notification("error", this.props.itemRequest.error);
      this.props.clearError();
    }

    if (this.props.itemRequest.success) {
      // this.props.history.goBack();
      notification("success", this.props.itemRequest.success);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateItemRequest } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing itemRequest code
    // value.customer_type_code = value.customer_type_code.toUpperCase();
    // console.log(value);
    updateItemRequest(id, value);
  };

  handleSubmitClose = value => {
    const { closeItemRequest } = this.props;
    const { id } = this.props.match.params;

    closeItemRequest(id, value);
  };

  render() {
    // console.log(this.props);
    const { loading, loadingSubmit, itemRequest, printData } = this.props.itemRequest;
    const modified = {...itemRequest, ...printData}

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="itemRequests.update.title" />}>
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryItemRequest
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={itemRequest ? itemRequest.description : null}
          />
          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <ItemRequestUpdateForm
                  submit={this.handleSubmit}
                  itemRequest={this.props.itemRequest}
                  handleSubmitClose={this.handleSubmitClose}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <ItemRequestDocUpdateForm
                  submit={this.handleSubmit}
                  itemRequest={this.props.itemRequest}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemRequest: state.itemRequest,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateItemRequest);
